<template>
    <div v-if="IsMobile">
        <div class="panel">
            <!-- panel header -->
            <div class="header">
                <div>{{ Unit.Data.Name }}</div>
                <Icon :fontSize="'14pt'" :iconType="'times'" :dimensions="'27px'" :withBackground="true" :callback="onClose"/>
            </div>

            <div style="display: flex; flex-flow: column; flex-direction: row; flex-wrap: nowrap; align-content: stretch;">
                <div class="mobile-panel-left" >
                    <!-- unit avatar -->
                    <img class="device-avatar" v-if="Unit.Preview" :src="Unit.Preview" />
                    <!-- unit description -->
                    <p class="device-description">{{ Unit.Data.Description }}</p>
                </div>

                <!-- real-time unit state variables -->
                <div v-if="Unit.Data.Variables" class="device-variables-mobile">
                    <table>
                        <tr v-for="(Variable, index) in Unit.Data.Variables" :key="index">
                            <td>{{ PrettyName(Variable) }}</td>
                            <td class="device-variables-value">{{ PrettyValue(Variable) }}</td>
                            <td class="device-variables-unit">{{ Variable.unit || FallbackUnit(Variable) }}</td>
                        </tr>
                    </table>
                </div>
                <div v-else>
                    <span style="text-align: center; width: 50%;">Reading device data ...</span>
                </div>
            </div>
        </div>
    </div>
    <div v-else>
        <div class="panel">
            <!-- panel header -->
            <div class="header">
                <div>{{ Unit.Data.Name }}</div>
                <Icon :fontSize="'14pt'" :iconType="'times'" :dimensions="'27px'" :withBackground="true" :callback="onClose"/>
            </div>

            <!-- unit avatar -->
            <img class="device-avatar" v-if="Unit.Preview" :src="Unit.Preview" />

            <!-- unit description -->
            <p class="device-description">{{ Unit.Data.Description }}</p>

            <!-- real-time unit state variables -->
            <div v-if="Unit.Data.Variables" class="device-variables">
                <table>
                    <tr v-for="(Variable, index) in Unit.Data.Variables" :key="index">
                        <td>{{ PrettyName(Variable) }}</td>
                        <td class="device-variables-value">{{ PrettyValue(Variable) }}</td>
                        <td class="device-variables-unit">{{ Variable.unit || FallbackUnit(Variable) }}</td>
                    </tr>
                </table>
            </div>
            <div v-else>
                <span>Reading device data ...</span>
            </div>
        </div>
    </div>
</template>

<script>
import { mobileCheck } from "@/utils";

/* components */
import Icon from "@/components/Icon.vue";

export default {
    props: 
    {
        "SelectedUnit" : Object,
        "onPanelClose": Function
    },
    data() 
    {
        return {
            name: "panelie panel",
            Unit: this.SelectedUnit,
            IsMobile: false,
        }
    },

    mounted()
    {
        this.IsMobile = mobileCheck();
        document.documentElement.style.setProperty('--details-panel-width', this.IsMobile ? '100vw' : '500px');
    },

    methods: 
    {
        onClose: function () { this.onPanelClose() },

        PrettyValue: function(DeviceVariable)
        {
            switch(DeviceVariable.type)
            {
                case "Boolean": { return DeviceVariable.value ? 'ON' : 'OFF'; }
                //case "Int": { return DeviceVariable.value; }
                case "Float": { return parseFloat(DeviceVariable.value).toFixed(2);  }
                //case "String": { return DeviceVariable.value; }
                //case "Date": { return DeviceVariable.value; }
                //case "Time": { return DeviceVariable.value; }

                default: { return DeviceVariable.value; }
            }
        },

        FallbackUnit: function(DeviceVariable)
        {
            const VariableUnits = {
                "AI_IntakeAirTemp":  "C", // 1
                "AI_SupplyAirTemp":  "C", // 2
                "AI_ExtractAirTemp":  "C", // 3
                "AI_ExhaustAirTemp":  "C", // 4
                "AI_EfficiencyTemp":  "C",// 5
                "AI_RoomTemp1":  "C", // 6
                "AI_EAFFlow":  "m/h", // 7
                "AI_SAFFlow":  "m/h",// 8
                "AI_FilterGuard1":  "pa",// 9
                "AI_FilterGuard2":  "pa",// 10
                "Efficiency":  "%", // 11
                "AI_HumidityDuct":  "%", // 12 
                "AI_HumidityRoom":  "%", // 13 
                "DO_3":  "",// 14
                "DO_7":  "",// 15
                "UnitMode":  "", // 16
                "AI_ExtraSensor1":  "kJ/kg", // 17
                "AI_ExtraSensor2":  "kJ/kg", // 18
                "AI_ExtraSensor3":  "kJ/kg", // 19
                "SFP":  "kW/m/s", // 20
                "AO_SeqY2":  "%", // 21
                "AO_ChangeOver1":  "%" // 22
            };

            return VariableUnits[DeviceVariable.name] || "";
        },

        PrettyName: function(DeviceVariable)
        {
            const VariableUnits = {
                "AI_IntakeAirTemp":  "Intake air temperature", // 1
                "AI_SupplyAirTemp":  "Supply air temperature", // 2
                "AI_ExtractAirTemp":  "Extract air temperature", // 3
                "AI_ExhaustAirTemp":  "Exhaust air temperature", // 4
                "AI_EfficiencyTemp":  "Efficiency air temperature",// 5
                "AI_RoomTemp1":  "Room air temperature", // 6
                "AI_EAFFlow":  "Air volume exhaust air fan", // 7
                "AI_SAFFlow":  "Air volume supply air fan",// 8
                // "AI_FilterGuard1":  "pa",// 9
                // "AI_FilterGuard2":  "pa",// 10
                "Efficiency":  "Efficiency", // 11
                // "AI_HumidityDuct":  "%", // 12 
                // "AI_HumidityRoom":  "%", // 13 
                // "DO_3":  "",// 14
                // "DO_7":  "",// 15
                // "UnitMode":  "", // 16
                // "AI_ExtraSensor1":  "kJ/kg", // 17
                // "AI_ExtraSensor2":  "kJ/kg", // 18
                // "AI_ExtraSensor3":  "kJ/kg", // 19
                "SFP":  "Specific fan power", // 20
                // "AO_SeqY2":  "%", // 21
                // "AO_ChangeOver1":  "%" // 22
            };

            return VariableUnits[DeviceVariable.name] || DeviceVariable.title;
        }
    },

    components: 
    {
        Icon
    }
}
</script>

