    --details-panel-width: 100vw;
}

.panel {

    position: fixed;
    top: 0;
    left: 0;

    width: var(--details-panel-width);
    height: 100vh;
    
    margin: 0;
    padding: 4px;

    display: flex;
    flex-flow: column;
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: stretch;
    justify-content: flex-start;
    align-items: stretch;

    box-sizing: border-box;

    color: #ffffff;
    background: rgba(50, 55, 59, 0.8);
    mix-blend-mode: normal;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.2); 

    z-index: 99;
}

.mobile-panel-left {
    width: 50%;
}

.header {
    
    margin: 0;
    padding: 8px;
    box-sizing: border-box;
    width: 100%;
    font-size: 18pt;
    display: flex;
    flex-direction: row;
    justify-content: space-between; 
}

.device-avatar {
    width: 100%;
    object-fit: cover;
}

.device-description {
    min-height: 100px;
    pointer-events: auto;
    text-align: justify;
    overflow-y: auto;

    /* read-only */
    user-select: none;
}

.device-variables {   
    overflow-y: scroll;
    pointer-events: auto;
    flex-grow: 1;
}

.device-variables-mobile {   
    overflow-y: scroll;
    pointer-events: auto;
    flex-grow: 1;
    height: calc(100vh - 50px);
}

