<template>
  <div class="slider-wrap">
    <vue-slider v-bind="{...options, disabled: isDisabled}" v-model="value" @change="this.onchange">
      <template v-slot:label="{ label, active }">
        <div :class="['vue-slider-mark-label', 'custom-label', { active }]">
          <font-awesome-icon  :class="[{ active }]" v-if="label === 'TOP'"  id="home-icon" :icon="'home'" />
          <div v-if="label !== 'TOP'">{{ label }}</div>
        </div>
      </template>
    </vue-slider>
    </div>
</template>

<script>
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'

export default {
  components: {
    VueSlider
  },
  props: {
            values: Array,
            initvalue: String,
            onchange: Function
     },
  data () {
    return {
      value: this.$store.state.floor,
      options: {
        dotSize: 24,
        width: 10,
        height: 200,
        contained: true,
        direction: 'btt',
	      data: this.values,
        marks: this.values,
        tooltip: "none",
        color: '#d6e0e7;'
      }
    }
  },
  computed: {
    isDisabled () { return this.$store.state.SelectedUnit.Serial !== undefined; }
  }
}
</script>

