<template>
  <div id="main" :style="cssVars">
    <div id="ui" :click="deselectUnit" >
        <img id="logo" src="./logo.svg"/>
        <Loading 
            :isLoading="!LoadingDone"
        />
        <EcoPanel v-if="LoadingDone" />
      <!-- <Icon
        :callback="onShowModal"
        class="question-mark"
        style="padding-top: 4px"
        :fontSize="'16pt'"
        :iconType="'question'"
        :dimensions="'36px'"
        :withBackground="true"
      /> -->
      <Modal />
      <DetailsPanel v-if="IsUnitSelected" :SelectedUnit="SelectedUnit" :onPanelClose="deselectUnit" />
      <div class="components">
        <Slider 
            v-if="!IsUnitSelected && LoadingDone"
            :values="floorSlider.values" 
            :initvalue="floorSlider.initvalue" 
            :onchange="setFloor"

        />
        <!-- <Progressbar :progress="50"  /> -->
      </div>
    </div>
  </div>
</template>

<script>
import InitializeScene from "@/threejs";

import { mapGetters, mapMutations, mapActions } from "vuex";

import Vue from "vue";
import VModal from "vue-js-modal";
Vue.use(VModal);

/** components */
import DetailsPanel from "@/components/DetailsPanel.vue";
import Button from "@/components/Button.vue";
import Modal from "@/components/Modal.vue";
import Slider from "@/components/Slider.vue";
import Progressbar from "@/components/Progressbar.vue";
import Font from "@/components/Font.vue";
import Icon from "@/components/Icon.vue";
import Loading from "@/components/Loading.vue";
import EcoPanel from "@/components/EcoPanel.vue";

/**types */
import { PanelData,  PAVILLION_FLOORS } from "@/types";

/** font-awesome stuff */
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { fas } from '@fortawesome/free-solid-svg-icons';
library.add(fas);

Vue.component("font-awesome-icon", FontAwesomeIcon);

const VueTippy = require("vue-tippy");
const { TippyComponent } = require("vue-tippy");

Vue.use(VueTippy);
Vue.component("tippy", TippyComponent);

Vue.config.productionTip = false;

export default Vue.extend({
    name: "Main",
    data() {
        return {
            bttnTitle: "Show Details Panel",
            floorSlider: {
                values: [ 
                    PAVILLION_FLOORS.GROUND_FLOOR,
                    PAVILLION_FLOORS.FIRST_FLOOR, 
                    PAVILLION_FLOORS.SECOND_FLOOR,
                    PAVILLION_FLOORS.WHOLE_PAVILLION_VIEW  
                ],
                initvalue: this.$store.state.floor,
                current: this.$store.state.floor
            },
        };
    },
    components: {
        DetailsPanel,
        Button,
        Slider,
        Progressbar,
        Font,
        FontAwesomeIcon,
        Icon,
        Modal,
        Loading,
        EcoPanel
    },

    computed: 
    {
        ...mapGetters([
            "Floor",
            "SelectedUnit",
            "IsUnitSelected",
            "LoadingDone"
        ]),

        cssVars() {
            return {
                "--pointer-events":
                this.$store.state.SelectedUnit.Serial ? "none" : "auto",
            };
        },
    },
    methods: 
    {
        ...mapMutations([
            "setFloor",
            "deselectUnit"
        ]),

        ...mapActions([
            "InitializeEcoPanel"
        ]),

        onShowModal: function () 
        {
            this.$modal.show("tutorial");
        },
    },

    async mounted() 
    {
        // Create and initialize ThreeJS scene.
        InitializeScene();     

        await this.InitializeEcoPanel();
    },
});
</script>

